import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { ratingGraduate } from 'dump/data';

const columns = [
  {
    title: 'Xếp hạng',
    dataIndex: 'graded',
    key: 'graded',
  },
  {
    title: 'Tên tiếng anh',
    dataIndex: 'englishName',
    key: 'englishName',
  },
  {
    title: 'Từ điểm',
    dataIndex: 'fromScores',
    key: 'fromScores',
  },
  {
    title: 'Đến điểm',
    dataIndex: 'toScores',
    key: 'toScores',
  },
  {
    title: 'Từ điểm thang 10',
    dataIndex: 'fromScoresTen',
    key: 'fromScoresTen',
  },
  {
    title: 'Đến điểm thang 10',
    dataIndex: 'toScoresTen',
    key: 'toScoresTen',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      ratingGraduate.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#/rating-graduate/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class RatingGraduatePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh mục xếp hạng tốt nghiệp">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/rating-graduate/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={ratingGraduate} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RatingGraduatePage />
  </Page>
);



